<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="problem-page create-action-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-show="onShow"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="智能维修">
        <!-- <van-icon name="ellipsis" slot="right" @click.native="onMoreAction" /> -->
        <i class="icon-wode nav-icon-right" slot="right" @click="handleToMe"></i>
      </van-nav-bar>

      <van-popup v-model="moreAction" class="header-more" :style="{top: moreTop + 'vw'}">
        <div class="more-action">
          <van-cell title="提报故障" clickable @click.native="handleCreateFault" />
          <van-cell title="提报问题" clickable @click.native="handleCreateProblem" />
        </div>
      </van-popup>

      <div class="search-bar">
        <van-search placeholder="搜索问题编号、摘要" v-model.lazy="filter.key" />
      </div>

      <van-dropdown-menu class="select-bar">
        <van-dropdown-item v-model="filter.status" :options="statusOptions" />
        <van-dropdown-item v-model="filter.profession" :options="professionOptions" />
        <van-dropdown-item :title="filter.beginDate" ref="beginDate">
          <van-datetime-picker
            v-model="beginDate"
            type="date"
            @cancel="onBeginDateCancel"
            @confirm="onBeginDateConfirm"
          />
        </van-dropdown-item>
        <van-dropdown-item :title="filter.endDate" ref="endDate">
          <van-datetime-picker
            v-model="endDate"
            type="date"
            @cancel="onEndDateCancel"
            @confirm="onEndDateConfirm"
          />
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <van-loading type="spinner" v-if="loading" class="page-loading" />

    <div v-if="!list.length" style="text-align: center;line-height: 100px;">暂无数据</div>

    <transition-group class="problem-list" tag="div" name="list">
      <item 
        @click.native="handleDetail(item)"
        v-for="(item) in list"
        :key="item.id"
        :data="item"
        v-if="list.length"
      >
      </item>
      
    </transition-group>

    <template slot="blank">
      <div class="fault-action" @click="handleCreateFault"></div>
      <div class="problem-action" @click="handleCreateProblem"></div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import Item from './Item'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import {problemStatusEnum} from '../../utils/enum.js'
import moment from 'moment'

export default {
  components: {
    Page,
    Item,
    // AScroll
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    moreAction: false,
    moreTop: 0,
    header: null,
    filter: {
      key: '',
      status: '',
      profession: '',
      pageIndex: 1,
      pageSize: 10,
      beginDate: moment()
        .startOf('years')
        .format('YYYY-MM-DD'),
      endDate: moment()
        .endOf('years')
        .format('YYYY-MM-DD')
    },
    problemStatusEnum: problemStatusEnum,
    list: [],
    loading: true,
    statusOptions: [
      {text: '全部', value: ''},
      {text: '待受理', value: 'wait_assigned'},
      {text: '跟进中', value: 'following_up'},
      {text: '待审核', value: 'pending_review'},
      {text: '已关闭', value: 'close'},
    ],
    professionOptions: [
      {text: '全部', value: ''},
    ],
    beginDate: new Date(),
    endDate: new Date()
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      console.log(JSON.stringify(header))
      console.log(JSON.stringify(main))

      this.headerHeightVW = (this.header.h + 95) * 100 / this.header.w
      this.moreTop = this.header.h * 100 / this.header.w

      this.onRefresh()
      // this.onLoadConfig()

      this.professionOptions = this.professionOptions.concat(
        ...JSON.parse(localStorage.getItem('problem_profession')).map(item => ({
          text: item.name,
          value: item.id
        }))
      )
    },

    onMoreAction () {
      this.moreAction = true
    },

    onLoadConfig () {
      io.getProblemConfig().then(data => {
        localStorage.setItem('problem_level', JSON.stringify(data.level))
        localStorage.setItem('problem_category', JSON.stringify(data.category))
        localStorage.setItem('problem_flow', JSON.stringify(data.processflow))
        localStorage.setItem('problem_address', JSON.stringify(data.address))
        localStorage.setItem('problem_profession', JSON.stringify(data.profession))
        localStorage.setItem('problem_source', JSON.stringify(data.source))
      })
    },

    onShow () {
      // this.onRefresh()
      if (localStorage.getItem('refresh_problem_id')) {
        io.getProblemListId({id: localStorage.getItem('refresh_problem_id')}).then(data => {
          
          const index = this.list.findIndex(item => item.id == data.detail.id)

          if (index >= 0) {
            this.$set(this.list, index, data.detail)
          }

          localStorage.setItem('refresh_problem_id', '')
        })
      }
    },

    onRefresh () {
      this.filter.pageIndex = 1

      io.getProblemList(this.filter).then(data => {
        this.list = data.list
        this.filter.pageIndex++
        this.$refs.page.finishRefresh()
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.finishRefresh()
        this.loading = false
      })
    },

    onLoad () {
      io.getProblemList(this.filter).then(data => {
        this.list = this.list.concat(data.list)
        this.filter.pageIndex++
        if (data.list.length == 0) {
          this.$refs.page.endLoad()
        } else {
          this.$refs.page.updateLoad()
        }
        this.loading = false
      }).catch(e => {
        this.$toast(e)
        this.$refs.page.updateLoad()
        this.loading = false
      })
    },

    handleDetail (item) {
      localStorage.setItem('refresh_problem_id', item.id)
      navigate.open('problemdetail', {id: item.id})
    },

    onBeginDateCancel () {
      this.$refs.beginDate.toggle()
    },

    onBeginDateConfirm (val) {
      console.log(val)
      this.filter.beginDate = moment(val).format('YYYY-MM-DD')
      this.$refs.beginDate.toggle()
      this.onRefresh()
    },

    onEndDateCancel () {
      this.$refs.endDate.toggle()
    },

    onEndDateConfirm (val) {
      console.log(val)
      this.filter.endDate = moment(val).format('YYYY-MM-DD')
      this.$refs.endDate.toggle()
      this.onRefresh()
    },

    handleCreateFault () {
      this.moreAction = false
      navigate.open('createFault')
    },

    handleCreateProblem () {
      this.moreAction = false
      navigate.open('createProblem')
    },

    handleToMe () {
      navigate.open('me')
    }

  },
  watch: {
    'filter.status': function (val) {
      this.onRefresh()
    },
    'filter.profession': function (val) {
      this.onRefresh()
    },
    'filter.key': function (val) {
      this.onRefresh()
    }
  }
}
</script>

