      }

      &.search-filter{
        background: url('../../assets/images/search/filter.png') no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}

.problem-page{
  .problem-list{
    padding: 16px;
  }

  .page-loading{
    margin: 0 auto;
